body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

navbar {
  width: auto;
  height: 50px;
  display: flex;
  padding: 10px 10px;
  background-color: #F55A5A;
  align-items: center;
}

.nav--img {
  position: absolute;
  left: 213px;
  top: 16px;

}

.nav--content {
  position: absolute;
  left: 270px;
  top: 15px;
  width: 109px;
  height: 16px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  letter-spacing: -0.005em;

  color: #FFFFFF;
}

.card--lists {
  display: flex;
  flex-direction: row;
  flex-direction: column;
  max-width: 800px;
  padding: 10px 10px;

}

.cards {
  padding: 40px;
  display: flex;
  float: left;
}

.card--img {
  width: 125px;
  height: 168px;
  border-radius: 5px
}

.card--contents {
  padding: 0px 10px;
   /* display: flex; 
  flex-direction: column;  */
}

.card--location {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 10.24px;
  letter-spacing: 0.17em;
  margin-bottom: 2px;

  color: #2B283A;
}

.card--date {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 700;
  font-size: 10.24px;
  margin-bottom: 2px;
  color: #2B283A;
}

.card--title {
  width: 326px;
  height: 40px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 700;
  font-size: 25px;
  line-height: 0px;
  margin-bottom: 0px;
  color: #2B283A;
}

.card--description {
  width: 326px;
  height: 45px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 10.24px;
  line-height: 150%;
  margin-top: 0px;
  /* or 15px */


  color: #2B283A;
}